<template>
  <div class="tags">
    <Tag
      v-for="tag in tags"
      :key="tag.id"
      :selected="isTagSelected(tag)"
      :show-delete="false"
      @click="
        selectTag(tag);
        $emit('tag-selected');
      "
      >{{ tag.title }}</Tag
    >
  </div>
</template>

<script>
import Tag from "@/components/Tag";
import { mapState, mapActions, mapGetters } from "vuex";
export default {
  components: {
    Tag
  },
  computed: {
    ...mapState(["tags", "selectedTags"]),
    ...mapGetters(["isTagSelected"])
  },
  methods: {
    ...mapActions(["selectTag"])
  }
};
</script>

