import Vuex from "vuex";
import Vue from "vue";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    tags: [],
    selectedTags: [],
    cards: [],
    cardsCopy: [],
    page: 1,
    loading: false
  },
  getters: {
    isTagSelected: state => ({ slug }) =>
      state.selectedTags.some(tag => tag.slug === slug),
    cards: state => {
      if (state.selectedTags.length <= 1) {
        return state.cards;
      }

      const filteredCards = [];
      for (let card of state.cards) {
        let hasAllTags = true;
        for (let tag of state.selectedTags) {
          if (!card.slugs.includes(tag.slug)) {
            hasAllTags = false;
          }
        }
        if (hasAllTags) {
          filteredCards.push(card);
        }
      }
      return filteredCards;
    }
  },
  mutations: {
    setLoading(state, loading) {
      state.loading = loading;
    },
    addTag(state, tag) {
      state.selectedTags.push(tag);
    },
    removeTag(state, { slug }) {
      state.selectedTags = state.selectedTags.filter(tag => tag.slug !== slug);
    },
    setTags(state, tags) {
      state.tags = tags;
    },
    setCards(state, cards) {
      state.cards = cards;
    },
    addCards(state, cards) {
      state.cards = [...state.cards, ...cards];
    },
    nextPage(state) {
      state.page++;
    },
    setPage(state, page) {
      state.page = page;
    }
  },
  actions: {
    selectTag({ commit, getters, dispatch, state }, tag) {
      if (getters.isTagSelected(tag)) {
        commit("removeTag", tag);
      } else {
        commit("addTag", { ...tag });
        if (state.selectedTags.length === 1) {
          commit("setPage", 1);
        }
      }
      if (state.selectedTags.length <= 1) {
        dispatch("fetchCards");
      }
    },
    async fetchTags({ commit }) {
      const { data } = await axios.get("/data/tags.json");
      commit("setTags", data);
    },
    async fetchCards({ commit, dispatch, state }) {
      commit("setLoading", true);
      setTimeout(async () => {
        let query = `page-${state.page}`;

        if (state.selectedTags.length > 0) {
          await dispatch("fetchCardsByTags");
          commit("setLoading", false);
          return;
        }

        const { data } = await axios.get(`/data/${query}.json`);

        if (state.page === 1) {
          commit("setCards", data);
        } else {
          commit("addCards", data);
        }

        commit("setLoading", false);
      }, 900);
    },
    async fetchCardsByTags({ commit, state }) {
      const query = `tags-${state.selectedTags[0].slug}-${state.page}`;

      const { data } = await axios.get(`/data/${query}.json`);
      if (state.page === 1) {
        commit("setCards", data);
      } else {
        commit("addCards", data);
      }
    }
  }
});
