<template>
  <div class="container is-fluid">
    <transition-group name="fade" tag="div" class="columns is-multiline">
      <div
        v-for="card in cards"
        :key="card.id"
        class="column is-full-mobile is-4-tablet is-3-desktop is-4-widescreen is-3-fullhd"
      >
        <a :href="card.url" target="_blank">
          <div class="card">
            <div class="card-image">
              <Tag :show-delete="false" v-if="card.isHighlighted"
                >Выбор редакции</Tag
              >

              <figure class="image">
                <img :src="card.image" class="is-16by9" />
              </figure>
            </div>
            <div class="card-content">
              <div class="content">
                {{ card.title }}
              </div>
            </div>
          </div>
        </a>
      </div>
    </transition-group>
    <div class="spinner" v-if="loading" />
  </div>
</template>

<script>
import Tag from "@/components/Tag";
import { mapGetters } from "vuex";

export default {
  components: {
    Tag
  },
  data() {
    return {
      loading: false
    };
  },
  computed: {
    ...mapGetters(["cards"])
  },
  created() {
    window.onscroll = () => {
      this.onScroll();
    };
  },
  methods: {
    onScroll() {
      if (this.loading) {
        return;
      }
      if (this.cards.length === 0) {
        return;
      }
      if (
        window.innerHeight + window.scrollY >=
        document.body.scrollHeight - 2
      ) {
        this.$store.commit("nextPage");
        this.loadCards();
      }
    },
    async loadCards() {
      this.loading = true;
      await this.$store.dispatch("fetchCards", {
        tags: this.$store.state.selectedTags
      });
      this.loading = false;
    }
  }
};
</script>

