  components: {
    Navbar,
    Cards,
    Tags,
    Popup
  },
  data() {
    return {
      showTags: false
    };
  },
  created() {
    this.$store.dispatch("fetchTags");
    this.$store.dispatch("fetchCards", { page: 1, tags: [] });
